.App {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-family: 'Open Sans', sans-serif;
    padding-top: 10px;
    color: #333;
}

body {
    background-color: #fffdfa;
    padding-bottom: 80px;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    max-width: 460px;
    margin-bottom: 20px;
}

.scoreTracker {
    align-self: flex-end;
    margin-right: 4px;
    background-color: #8f8787;
    color: white;
    padding: 2px 10px;
    font-weight: 900;
    font-size: 18px;
    border-radius: 4px;
    margin-bottom: 4px;
}

.board {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 3px 0;
    background-color: #d5c6b9;
    border-radius: 8px;
    position: relative;
}

.piece {
    background-color: #f4ede6;
    height: 70px;
    width: 30.4%;
    cursor: pointer;
    margin: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    border-radius: 3px;
    font-size: 17px;
    font-family: 'Roboto';
    user-select: none;
    color: #454545;
}

.piece.correct {
    background-color: #5c97bf;
    color: white;
}

.piece.incorrect {
    color: white;
    background-color: #e26a6a;
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.overlay {
    position: absolute;
    background: rgba(213, 198, 185, 0.89);
    justify-content: center;
    align-items: center;
    display: flex;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    font-weight: 900;
    font-size: 32px;
    color: #444;
    font-family: 'Roboto', sans-serif;
    flex-direction: column;

    animation: fadein 0.6s;
}

.button {
    cursor: pointer;
    margin-top: 8px;
    background-color: #8f8787;
    padding: 8px 16px;
    user-select: none;
    color: #fff;
    font-weight: 700;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    border-radius: 4px;
    margin-bottom: 4px;
}

.clueText {
    margin-bottom: 30px;
    user-select: none;
    background-color: #5a5e5e;
    font-size: 14px;
    color: #ddd;
    font-weight: 800;

    display: flex;
    text-align: center;
    padding: 8px 18px;
    border-radius: 4px;
    align-items: center;
    justify-content: center;
}

.clue {
    color: white;
    font-size: 21px;
    margin-top: -1px;
    margin-left: 12px;
}

.helpText {
    margin-bottom: 40px;
    font-size: 16px;
    max-width: 540px;
    font-weight: 600;
    padding: 0 10px;
}

.title {
    margin-bottom: 10px;
    font-weight: 900;
    font-size: 30px;
    color: #444;
    font-family: 'Roboto', sans-serif;
}

.hr {
    border-top: 1px solid #ddd;
}

.disclaimer {
    margin-top: 30px;
    font-size: 14px;
    padding: 0 10px;
    max-width: 540px;
}
